  .menu-desplazable {
    width: 50%;
    background: #bdc3c7;  
    background: linear-gradient(to right, #2c3e50, #bdc3c7); 
    cursor: pointer;
    transition: all 0.4s ease;
    position: absolute;
    -webkit-transform: translate(100%);
            transform: translate(100%);
    text-align: inherit;
  }

  .menu-texto {
    font-size: 16px;
    font-size: var(--font-size-3-320);
