    color: black;
    text-decoration: underline;
  }
  .style:focus {
    color: #060a24;
    font-weight: bolder;
  }
  
   .style {
    color: whitesmoke;
    text-decoration: none;
  }
  
  #desplegar {
    display: none;
  }
  
  #desplegar:checked ~ .menu-desplazable {
    -webkit-transform: translate(-12% , 6.6%);
            transform: translate(-12% , 6.6%);
  }
   .linea {
    height: 10%;
    width: 75%;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: all 0.2s ease;
  }
  
  .linea-top {
    top: 25%;
  }
  
  .linea-low {
    top: 75%;
  }
  
  .label-menu {
    display: inline-block;
    height: 80%;
    width: 90%;
    padding: 0.1rem;
    margin-top: 2px;
    cursor: pointer;
    position: relative;
  }

  .custom-checkbox:checked + .label-menu .linea-low {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
    background-color: red;
    
  }
  
  .custom-checkbox:checked + .label-menu .linea-med {
    visibility: hidden;
  }
  
  .custom-checkbox:checked + .label-menu .linea-top {
    top: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    background-color: red;
  }
  
