.btn-order{
    background-color: #00c4ff26;
    height: 30px;
    width: 55px;
    border-radius: 20px;
    border: 1px solid black;
    outline: none;
    text-align: center;
    margin-right: 10px;
}

@media (max-width: 746px){
  .btn-order{
    margin: 10px auto;
    width: 100px;
    font-size: 17px;
    justify-content: center;
display: flex;
align-items: center;
}  
}
